export default {
    data() {
        return {
            hint: false,   //  忘记密码弹窗
            user: "",   // 账号
            password: "",   // 密码
            pwdType: "password",
            demo1: true,   // 是否记住密码
            list1: [['当前状态','空闲', '忙碌', '离开']],  // 可选服务状态
            value1: ['空闲'],  //当前服务状态
        }
    },
    mounted(){
        this.getCookie();
    },
    methods:{
        submitForm(){
            if(this.demo1){
                this.setCookie(this.user, this.password, 7);
                this.login();
            }else{
                //清空Cookie
                this.clearCookie();
                this.login();
            }
        },
        //设置cookie
        setCookie(c_name, c_pwd, exdays) {
            var exdate = new Date(); //获取时间
            exdate.setTime(exdate.getTime() + 24 * 60 * 60 * 1000 * exdays); //保存的天数
            //字符串拼接cookie
            window.document.cookie = "userName" + "=" + c_name + ";path=/;expires=" + exdate.toGMTString();
            window.document.cookie = "userPwd" + "=" + c_pwd + ";path=/;expires=" + exdate.toGMTString();
        },
        //读取cookie
        getCookie() {
            if (document.cookie.length > 0) {
                var arr = document.cookie.split('; '); //这里显示的格式需要切割一下自己可输出看下
                for (var i = 0; i < arr.length; i++) {
                    var arr2 = arr[i].split('='); //再次切割
                    //判断查找相对应的值
                    if (arr2[0] == 'userName') {
                        this.user = arr2[1]; //保存到保存数据的地方
                    } else if (arr2[0] == 'userPwd') {
                        this.password = arr2[1];
                    }
                }
            }
        },
        //清除cookie
        clearCookie() {
            this.setCookie("", "", -1); //修改2值都为空，天数为负1天就好了
        },
        // 显示密码
        conceal(){
            this.pwdType = this.pwdType === 'password'?'text':'password';
        },
        // 隐藏密码
        show(){
            this.pwdType = this.pwdType === 'password'?'text':'password';
        },
        // 登录按钮
        login(){
            var that =this;
            this.common.commonFun.ajax({
                dom: this,
                url: this.common.apiUrl.login,
                type: "post",
                data: {
                    account: this.user,
                    password: this.password,
                },
                success: (response, allResponse) => {
                    window.localStorage.setItem("access_token", allResponse.data.access_token);
                    window.localStorage.setItem("user_id", allResponse.user_id);
                    window.localStorage.setItem("statistics", allResponse.statistics);
                    if(!window.localStorage.getItem("pc_access_token")) {
                        that.$router.push({
                            name : 'call',
                            params:{
                                come: 1
                            }
                        })
                    }
                }
            });
        },
        //  改变服务状态是触发
        onChange () {
            console.log('当前的值是', this.value1[0])
        },
    }
}



